const state = {
    loggedIn: false,
    info: {}
}

const getters = {

}
const mutations = {
    login(state, data){
        state.loggedIn = true;
        state.info = data;
        api.defaults.headers.common["Token"] = state.info.token;

        // localStorage.setItem("eStudy", JSON.stringify({loggedIn: true, info: JSON.stringify({data})}))
    },
    updataToken(state, token){
        state.info.token = token
    },
    logout(state){
        state.info = {};
        state.loggedIn = false;
        api.defaults.headers.common["Token"] = '';

    }
}
const actions = {
    login({commit}, form){
        api.post("/auth/login", form).then(result => {
            commit("login", result);
            console.log(result)
        })
    },
    checkedLogin({commit, state, dispatch}){
        api.defaults.headers.common["Token"] = state.info.token
        api.post("/auth/refreshToken", {}, {_slient: true}).then((token)=>{
            commit("updataToken", token)
        }).catch(()=>{
            dispatch("logout")
        })
    },
    logout({commit}){
        commit("logout")
    }
}
export default {
    namespaced: true, //命名空间
    state,
    getters,
    mutations,
    actions
}