<template>
  <a-layout id="app">
    <a-layout-header class="header flexrow">
      <h2>蝉壳 eStudy</h2>
      <div class="right flexrow" v-if="loggedIn">
        <div v-if="isTeacher" class="flexrow">
          <h4>{{ info.name }}</h4>
          <a-button icon="file-add" class="vcenter" type="primary">新建作业</a-button>
        </div>
        <div v-else class="flexrow">
          <h4 class="flexcol">
            <span>{{ info.full_name }}</span>
            <span>学号：{{ info.name }}</span>
          </h4>
          <h5 class="flexrow">
            <span v-for="(org, index) in student.orgs" :key="`${org}_${index}`">{{ org }}</span>
          </h5>
          <div>
            <span class="label">{{ student.stats.excellent }}个作业待提交</span>
            <span class="label">{{ student.stats.revising }}个作业待批改</span>
            <span class="label">{{ student.stats.improvable }}个作业需完善</span>
            <span class="label">{{ student.stats.uncommitted }}个作业已完成</span>
          </div>
        </div>
        <a href="http://vipgit.chanke.xyz" target="_black" class="vcenter">
          <a-button icon="home">代码仓库</a-button>
        </a>
        <a-button icon="logout" class="vcenter" @click="$store.dispatch('user/logout')">退出</a-button>
      </div>
    </a-layout-header>
    <a-layout-content>
      <Authorization v-if="!loggedIn"></Authorization>
      <div v-else>
        <Teacher v-if="isTeacher" :teacher="teacher"></Teacher>
        <Student v-else :student="student"></Student>
      </div>
    </a-layout-content>
  </a-layout>
</template>

<script>
import { mapState } from "vuex";
import { api } from "./utils/api";
import Authorization from "./components/Authorization";
import Teacher from "./components/Teacher";
import Student from "./components/Student";
export default {
  name: "app",
  data() {
    return {
      student: {
        orgs: [],
        stats: {},
        assignments: []
      },
      teacher: {
        orgs: [],
        assignments: []
      }
    };
  },
  computed: {
    ...mapState("user", ["info", "loggedIn"]),
    isTeacher() {
      console.log(this.$store.state.user.info.is_admin);
      return this.$store.state.user.info.is_admin > 0;
    }
  },
  created() {
    // 判断是否自动登录
    if (this.loggedIn) {
      this.loadDtrails();
    }
  },
  watch: {
    // 监听loggedIn，如果改变就会触发
    loggedIn(val) {
      if (val) {
        this.loadDtrails();
      }
    }
  },
  methods: {
    loadDtrails() {
      // 发送ajax请求
      if (this.isTeacher) {
        api.get("/teacher/detail").then(data => {
          // console.log(data);
          this.teacher.orgs = data.orgs;
          this.teacher.assignments = data.assignments;
        });
      } else {
        api.get("/student/detail").then(data => {
          // console.log(data);
          this.student.orgs = data.orgs;
          this.student.assignments = data.assignments;
          this.student.stats = data.stats;
        });
      }
    },
    handleClick() {
      console.log(123);
    }
  },
  components: {
    Authorization,
    Teacher,
    Student
  }
};
</script>

